import React from 'react';
import { List, Radio, Toast, Button, Icon } from 'antd-mobile';
import './page.scss';

function failToast() {
  Toast.offline('', 1);
}

const RadioItem = Radio.RadioItem;

class Page extends React.Component {
  constructor (props) {
    super(props);
    this.submit = this.submit.bind(this);
    this.next = this.next.bind(this);
    this.state = {
      value: 0,
      showNext: false,
      disabled: false,
      correct: false,
    };
  }
  
  submit() {
    if (this.state.value) {
      this.setState({
        showNext: true,
        disabled: true,
      });
      console.log(this.props.answer)
      console.log(this.state.value)
      if (this.props.answer === this.state.value) {
        this.setState({
          correct: true,
        });
      }
    } else {
      failToast()
    }
  }
  
  next() {
      this.props.nextPage();
  }
  
  onChange = (value) => {
    console.log(value);
    this.setState({
      value,
    });
  };
  
  render() {
    const value = this.state.value;
    const listItems = this.props.options.map((item, index) => {
      let key = '';
      switch (index) {
        case 0:
          key = 'A';
          break;
        case 1:
          key = 'B';
          break;
        case 2:
          key = 'C';
          break;
        case 3:
          key = 'D';
          break;
        default:
          break;
      }
      return (
        <RadioItem key={index} checked={value === item} onChange={() => this.onChange(item)} disabled={this.state.disabled}>
          {key}{item}
        </RadioItem>
      )
    });
    return (
      <div className="page">
        <div className="question">{this.props.question}</div>
        <div className="options">
          <List>
            {listItems}
          </List>
        </div>
        {this.state.showNext ? (
          <div className="answer">
            {
              this.state.correct ? (
                <div className="correct">
                  <Icon type="check-circle-o" /> 
                </div>) : (
                  <div className="wrong">
                  <Icon type="cross-circle" /> 
                  <p className="true-answer">{this.props.answer}</p>
              </div>)
            }
          </div>
          ) : null }
          <div className="btn-wrap">
            {!this.state.showNext ? <Button inline type="primary" className="next" size="small" onClick={this.submit}></Button> : null}
            {this.state.showNext ? <Button inline type="ghost" className="next" size="small" onClick={this.next}></Button> : null}
          </div>
      </div>
    )
  }
}

export default Page;


